import { useState } from "react"
import '../styles.css'

export function ClickIcon(props: {isPicked: boolean, code: string, urlPattern: string, handleClickEvent: any}) {

    const [pick, setPick] = useState<boolean>(props.isPicked)

    const handleClick = (e: any) => {
        e.preventDefault()
        const newPick = !pick
        setPick(newPick)
        props.handleClickEvent({e, pick: newPick, code: props.code})
    }

    return (
        <input className={(pick ? 'light' : 'grey') + ' filter-icon'} style={{}} onClick={handleClick} type="image" src={props.urlPattern.replace('%s', props.code)} />
    )
}
