import axios from 'axios'
import { SetStateAction } from 'react'

export async function accounts(updateFunc: SetStateAction<any>, shuffle = true) {
    // const response = await axios.get('https://api.slimedat.store/products')
    const response = await axios.get('http://localhost:4000/products')
    const accountsData = Array.isArray(response.data) && shuffle
        ? response.data.sort((a, b) => 0.5 - Math.random())
        : response.data

    updateFunc(accountsData)
    return accountsData
}