// This is an autoRemove method on the filter function that
// when given the new filter value and returns true, the filter
// will be automatically removed. Normally this is just an undefined
// check, but here, we want to remove the filter if it's not a number
filterGreaterThan.autoRemove = (val: any) => typeof val !== 'number'

function SelectColumnFilter({
  column: { filterValue, render, setFilter, preFilteredRows, id },
}: FilterProps<Account>) {
  const options = React.useMemo(() => {
    const options = new Set<any>()
    preFilteredRows.forEach((row) => {
      options.add(row.values[id])
    })
    return [...Array.from(options.values())]
  }, [id, preFilteredRows])

  return (
    <TextField
      select
      label={render('Header')}
      value={filterValue || ''}
      onChange={(e) => {
        setFilter(e.target.value || undefined)
      }}
      variant='standard'
    >
      <MenuItem value={''}>All</MenuItem>
      {options.map((option, i) => (
        <MenuItem key={i} value={option}>
          {option}
        </MenuItem>
      ))}
    </TextField>
  )
}

const getMinMaxAR = (rows: Row<Account>[], id: IdType<Account>) => {
  let min = rows.length ? rows[0].values[id].ar : 0
  let max = rows.length ? rows[0].values[id].ar : 0
  rows.forEach((row) => {
    min = Math.min(row.values[id].ar, min)
    max = Math.max(row.values[id].ar, max)
  })
  return [min, max]
}

function ARSliderColumnFilter({
  column: { render, filterValue, setFilter, preFilteredRows, id },
}: FilterProps<Account>) {
  const [min, max] = getMinMaxAR(preFilteredRows, id)
  const [value, setValue] = React.useState<number[]>(filterValue || [max, min])
  const [timeoutValue, setTimeoutValue] = React.useState<any>()

  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
      }}
    >
      <div style={{
        marginRight: '10px'
      }}>
        {render('Header')}
      </div>
      <Slider
        size="small"
        getAriaLabel={() => id}
        value={value}
        onChange={(event: Event, newValue: number | number[]) => {
          clearTimeout(timeoutValue)
          setTimeoutValue(setTimeout(() => setFilter(newValue), 300))
          setValue(newValue as number[])
        }}
        valueLabelDisplay="auto"
        getAriaValueText={() => id}
        min={min}
        max={max}
      />
    </div>
  )
}

function WeaponFilter({
  column: { render, filterValue, setFilter, preFilteredRows, id },
}: FilterProps<Account>) {

  const filteredValues = filterValue || []

  return (
    <div
      style={{
        justifyContent: 'space-between',
        alignItems: 'center',
      }}
    >
      {
        filteringWeapons.map(weapon => {
          return <ClickIcon
            key={weapon}
            isPicked={filteredValues.includes(weapon)}
            code={weapon.replace('\'', '')}
            urlPattern='/static/images/weapons/%s.png'
            handleClickEvent={
              (props: any) => {
                const codeIdx = filteredValues.indexOf(props.code)
                if (props.pick) {
                  codeIdx == -1 && filteredValues.push(props.code)
                } else {
                  codeIdx > -1 && filteredValues.splice(codeIdx, 1)
                }
                setFilter(filteredValues)
              }
            }
          />
        })
      }
    </div>
  )
}

function CharacterFilter({
  column: { render, filterValue, setFilter, preFilteredRows, id },
}: FilterProps<Account>) {

  const filteredValues = filterValue || []

  return (
    <div
      style={{
        justifyContent: 'space-between',
        alignItems: 'center',
      }}
    >
      {
        filteringCharacters.map(char => {
          return <ClickIcon
            key={char}
            code={char}
            isPicked={filteredValues.includes(char)}
            urlPattern='/static/images/characters/%s.png'
            handleClickEvent={
              (props: any) => {
                const codeIdx = filteredValues.indexOf(props.code)
                if (props.pick) {
                  codeIdx == -1 && filteredValues.push(props.code)
                } else {
                  codeIdx > -1 && filteredValues.splice(codeIdx, 1)
                }
                setFilter(filteredValues)
              }
            }
          />
        })
      }
    </div>
  )
}

function includeAll(source: any[], target: any[]) {
  return target.every(v => source.includes(v))
}

function CharacterFilterAction(rows: any[], columnIds: string[], filterValue: any) {
  return rows.filter(row => {
    return row.values['props.characters'] &&
      includeAll(row.values['props.characters'].map((v: any) => v.code), filterValue)
  })
}

function ARInMetaFilterAction(rows: any[], columnIds: string[], filterValue: any) {
  return rows.filter(row => {
    const ar = row.values['meta']?.ar || 0
    return filterValue[0] <= ar && ar <= filterValue[1]
  })
}

const useActiveElement = () => {
  const [active, setActive] = React.useState(document.activeElement)

  const handleFocusIn = () => {
    setActive(document.activeElement)
  }

  React.useEffect(() => {
    document.addEventListener('focusin', handleFocusIn)
    return () => {
      document.removeEventListener('focusin', handleFocusIn)
    }
  }, [])

  return active
}

function WeaponFilterAction(rows: any[], columnIds: string[], filterValue: any) {
  return rows.filter(row => {
    return row.values['props.weapons'] &&
      includeAll(row.values['props.weapons'].map((v: any) => v.code), filterValue)
  })
}

function CopyRowToClipboard(allRowsData: any) {
  navigator.clipboard.writeText(allRowsData.value)
}

// This is a custom UI for our 'between' or number range
// filter. It uses two number boxes and filters rows to
// ones that have values between the two
function NumberRangeColumnFilter({
  column: { filterValue = [], render, preFilteredRows, setFilter, id },
}: FilterProps<Account>) {
  const [min, max] = React.useMemo(() => getMinMaxAR(preFilteredRows, id), [id, preFilteredRows])
  const focusedElement = useActiveElement()
  const hasFocus = focusedElement && (focusedElement.id === `${id}_1` || focusedElement.id === `${id}_2`)
  return (
    <>
      <InputLabel htmlFor={id} shrink focused={!!hasFocus}>
        {render('Header')}
      </InputLabel>
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'baseline',
          paddingTop: 5,
        }}
      >
        <TextField
          id={`${id}_1`}
          value={filterValue[0] || ''}
          type='number'
          onChange={(e) => {
            const val = e.target.value
            setFilter((old: any[] = []) => [val ? parseInt(val, 10) : undefined, old[1]])
          }}
          placeholder={`Min (${min})`}
          style={{
            width: '70px',
            marginRight: '0.5rem',
          }}
          variant='standard'
        />
        to
        <TextField
          id={`${id}_2`}
          value={filterValue[1] || ''}
          type='number'
          onChange={(e) => {
            const val = e.target.value
            setFilter((old: any[] = []) => [old[0], val ? parseInt(val, 10) : undefined])
          }}
          placeholder={`Max (${max})`}
          style={{
            width: '70px',
            marginLeft: '0.5rem',
          }}
          variant='standard'
        />
      </div>
    </>
  )
}

