const App = () => {
  const windowSize = useWindowDimensions()
  const [data, setData] = useState([])

  useEffect(() => {
    accounts(setData)
  }, [])

  return (
    <Page>
      <CssBaseline />
      <div className="header-container">
        <div>
          <img className='header-left' src="/static/images/transparent_store_logo.png" />
        </div>
        <div className='header-right'>
          <div className="buy-here">Lin H</div>
          <IconButton aria-label="Slime Dat FB" onClick={() => window.open('https://facebook.com/slimedat')}>
            <FacebookIcon sx={{ color: '#2a2222', fontSize: '40px' }} />
          </IconButton>
        </div>
      </div>
      <Table<Account>
        name={'AccountTable'}
        columns={columns}
        data={data}
      />
    </Page>
  )
}

export default App
