import { Avatar, Chip } from '@mui/material'
import { ReactElement, useCallback } from 'react'
import type { ColumnInstance, FilterValue, IdType, TableInstance } from 'react-table'
import { makeStyles } from 'tss-react/mui'

const useStyles = makeStyles()({
  filtersActiveLabel: {
    color: '#998',
    fontSize: '14px',
    paddingRight: 10,
  },
  chipZone: {
    padding: '18px 0 5px 10px',
    width: '100%',
  },
  chipLabel: {
    fontWeight: 500,
    marginRight: 5,
  },
  filterChip: {
    marginRight: 4,
    color: '#222',
  },
})

interface FilterChipBarProps<T extends Record<string, unknown>> {
  instance: TableInstance<T>
}

const getFilterValue = (column: ColumnInstance<any>, filterValue: FilterValue) => {
  if (column.filter || column.id === 'meta') {
    const min = filterValue[0]
    const max = filterValue[1]
    return min ? (max ? `${min}-${max}` : `>=${min}`) : `<=${max}`
  }
  return filterValue
}

export function FilterChipBar<T extends Record<string, unknown>>({
  instance,
}: FilterChipBarProps<T>): ReactElement | null {
  const { classes } = useStyles()
  const {
    allColumns,
    setFilter,
    state: { filters },
  } = instance
  const handleDelete = useCallback(
    (id: string | number) => {
      setFilter(id as IdType<T>, undefined)
    },
    [setFilter]
  )
  const handleDeleteIcon = useCallback(
    (id: string | number, filterValue: any[], removeValue: string) => {
      const removeIdx = filterValue.indexOf(removeValue)
      removeIdx > -1 && filterValue.splice(removeIdx, 1)
      // setFilter(id as IdType<T>, filterValue)
      setFilter(id as IdType<T>, filterValue.length ? filterValue : undefined)
    },
    [setFilter]
  )

  return Object.keys(filters).length > 0 ? (
    <div className={classes.chipZone}>
      <span className={classes.filtersActiveLabel}>Lc/Filter:</span>
      {filters &&
        allColumns.map((column) => {
          const filter = filters.find((f) => f.id === column.id)
          const value = filter?.value
          const isCharacter = ('props.characters' === column.id)
          const isWeapon = ('props.weapons' === column.id)
          return (
            value && (
              (isCharacter || isWeapon)
                ? value.map((key: string) => {
                  return <Chip
                    className={classes.filterChip}
                    key={column.id + '_' + key}
                    avatar={<Avatar alt={key} src={'/static/images/' + (isCharacter ? 'characters' : 'weapons') + '/' + key + '.png'} />}
                    onDelete={() => handleDeleteIcon(column.id, value, key)}
                    variant='outlined'
                  />
                })
                :
                <Chip
                  className={classes.filterChip}
                  key={column.id}
                  label={
                    <>
                      <span className={classes.chipLabel}>{column.render('Header')}: </span>
                      {getFilterValue(column, value)}
                    </>
                  }
                  onDelete={() => handleDelete(column.id)}
                  variant='outlined'
                />
            )
          )
        })}
    </div>
  ) : null
}
